.button {
    border-radius: 3px;
    padding: 5px 24px;
    width: 149px;
    height: 30px;
}

.disabled {
    background-color: #004D99;
}

.secondary {
    color: #06c;
}

.default {
    background-color: #0066CC;
}

.red {
    background-color: #CC1439;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    color: #06c;
    background: #e5f0fa;
}


.default:active {
    background: #0059b2;
}

.secondary:active {
    background: #c9e5ff;
}

.red:active {
    background: #b21232;
}

.button:disabled {
    background: #004d99;
    opacity: 0.5;
}
